
import { defineComponent, computed, ref } from "vue";
import FormInput from "./FormInput.vue";
import { required, length, Status, validate } from "../validation";
import { User, useStore } from "../store";
import { useModal } from "../useModal";

export default defineComponent({
  components: {
    FormInput,
  },
  setup() {
    const username = ref("username");
    const usernameStatus = computed<Status>(() => {
      return validate(username.value, [required(), length({ min: 2, max: 10 })]);
    });

    const password = ref("password");
    const passwordStatus = computed<Status>(() => {
      return validate(password.value, [required(), length({ min: 8, max: 32 })]);
    });

    const store = useStore();
    const modal = useModal();

    const handleSumblit = async (evt: Event) => {
      if (!usernameStatus.value.valid || !passwordStatus.value.valid) {
        return
      }
      const newUser: User = {
        id: "-1",
        username: username.value,
        password: password.value,
      };

      await store.createUser(newUser);
      modal.hideModal();
    };

    return {
      usernameStatus,
      username,
      passwordStatus,
      password,

      handleSumblit,
    };
  },
});
